import { Box, FormControl, InputLabel, MenuItem, Select } from '@mui/material';

function Sort({ sortType, onChangeSort }) {
  const handleChange = (event) => {
    onChangeSort(event.target.value);
  };

  return (
    <Box
      sx={{
        minWidth: { xs: '250px', sm: '250px', md: '150px', lg: '160px' },
        m: { xs: '10px', sm: '10px', md: '10px', lg: '10px' },
      }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label"> </InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={sortType}
          label=" "
          onChange={handleChange}>
          <MenuItem value={'rating'}> (DESC)</MenuItem>
          <MenuItem value={'-rating'}> (ASC)</MenuItem>
          <MenuItem value={'price'}> (DESC)</MenuItem>
          <MenuItem value={'-price'}> (ASC)</MenuItem>
          <MenuItem value={'title'}> (DESC)</MenuItem>
          <MenuItem value={'-title'}> (ASC)</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
}

export default Sort;
